.section {
   &__review {
      background: url(..//img/review-bg.png) center no-repeat;
      background-size: cover;
   }
}
.review {
   &__swiper {
      .slide {
         display: flex;
         justify-content: center;
         align-items: center;
         flex-direction: column;
         text-align: center;
         &__text {
            font-family: "Work Sans";
            font-weight: 400; 
            font-size: 22px;
            line-height: 1.4;
            color: #fcfefe;
            @media (max-width: 480px){
               font-size: 18px;
            } 
            &:nth-child(2){
               position: relative;
               margin: 40px 0;
               &::after{
                  content: '';
                  width: 222px;
                  height: 1px;
                  position: absolute;
                  left: 50%;
                  bottom: -20px;
                  transform: translate(-50%, 0);
                  background: #BFD2F8;
               }
            }
         }
      }
   }
}
