.section__blog-post {
   .blog__post-text {
      margin: 32px 0 64px;
   }
}
.blog {
   display: flex;
   gap: 20px;
   @media (max-width: 768px) {
      flex-direction: column;
      gap: 60px;
   }
   &__post-buttons {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 400px){
         flex-direction: column;
         justify-content: center;
         gap: 10px;
         .blog__post-btn{
            max-width: 200px;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
         }
      }
   }
   &__post-prev {
      img {
         transform: rotate(-180deg);
      }
   }
   &__body {
      display: flex;
      flex-direction: column;
      gap: 32px;
   }
   &__post {
   }
   &__post-img {
      width: 100%;
      height: auto;
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__post-info {
      display: flex;
      align-items: center;
      gap: 24px;
      @media (max-width: 1004px) {
         gap: 18px;
         flex-wrap: wrap;
      }
      margin: 24px 0 8px;
   }
   &__post-item {
      display: flex;
      align-items: center;
      gap: 8px;
   }
   &__post-text {
      &:nth-child(4) {
         margin-bottom: 24px;
      }
   }
   &__post-title {
      margin-bottom: 16px !important;
      text-align: left !important;
   }
   &__post-btn {
      padding: 13px 26px;
      background: #bfd2f8;
      border-radius: 50px;
      display: inline-flex;
      align-items: center;
      gap: 8px;
      color: #1f2b6c !important;
      font-weight: 500 !important;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         background: #a2b3d5;
      }
   }
   &__aside {
      min-width: 314px;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      gap: 24px;
      @media (max-width: 1004px) {
         min-width: 280px;
      }
   }
   &__aside-form {
      display: flex;
      align-items: center;
      background: #1f2b6c;
      border-radius: 5px;
      width: 100%;
      height: 50px;
   }
   &__aside-input {
      width: 100%;
      height: 100%;
      border: none;
      background: none;
      padding: 0 20px;
      color: rgba(252, 254, 254, 0.8) !important;
      &::placeholder {
         color: rgba(252, 254, 254, 0.8) !important;
      }
      &::-moz-placeholder {
         color: rgba(252, 254, 254, 0.8) !important;
         opacity: 1;
      }
   }
   &__aside-btn {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 17px;
      cursor: pointer;
      border: none;
      background: none;
   }
   &__aside-title {
      margin-bottom: 24px !important;
      text-align: left !important;
   }
   &__aside-items {
      background: rgba(252, 254, 254, 0.2);
      border: 1px solid #1f2b6c;
      border-radius: 5px;
      padding: 20px;
   }
   &__aside-item {
      display: flex;
      align-items: center;
      gap: 10px;
      border-radius: 5px;
      margin-bottom: 8px;
      @media (max-width: 768px) {
         justify-content: center;
      }
      img {
         border-radius: 5px;
      }
   }
   &__aside-subtitle {
      font-family: "Work Sans";
      font-weight: 400;
      font-size: 12px;
      color: #159eec;
      margin-bottom: 5px;
   }
   &__aside-text {
      font-family: "Work Sans";
      font-weight: 500;
      font-size: 14px;
      color: #212124;
   }
   &__aside-category {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-radius: 5px;
      background: rgba(252, 254, 254, 0.2);
      padding: 12px 20px;
   }
   &__aside-label {
      color: #fcfefe !important;
      background: #159eec;
      border-radius: 50px;
      padding: 1px 7px;
   }
   &__pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      @media (max-width: 480px) {
         justify-content: space-between;
      }
   }
   &__pagination-items {
      display: flex;
      align-items: center;
      gap: 10px;
   }
   &__pagination-item {
      color: #159eec !important;
      &.active {
         color: #212124 !important;
      }
   }
   &__pagination-btn {
      display: flex;
      align-items: center;
      gap: 10px;
      position: absolute;
      right: 0;
      color: #159eec !important;
      img{
         transition: all 0.3s ease;
      }
      &:hover {
         img{
            transition: all 0.3s ease;
            transform: translate(5px, 0);
         }
      }
   }
}
