*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	opacity: 0;
	transition: all .3s ease;
	&.loaded{
		opacity: 1;
		transition: all .3s ease;
	}
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	font-style: normal;
	line-height: 1.16;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	width: 100%;
	@media (max-width: $md1) {
		max-width: 750px;
	}
	@media (max-width: $md2) {
		padding: 0 10px;
	}
}
.section {
	padding: 64px 0;
	overflow-x: hidden;
}
// main
.title {
	font-family: "Yeseva One";
	font-weight: 400;
	font-size: 32px;
	text-align: center; 
	color: #1f2b6c;
	margin-bottom: 64px;
	@media (max-width: 480px) {
		font-size: 26px;
	}
}
.title-big {
	font-family: "Yeseva One";
	font-weight: 400;
	font-size: 48px;
	@media (max-width: 480px) {
		font-size: 36px;
	}
	@media (max-width: 350px) {
		font-size: 32px;
	}
}
.subtitle {
	font-family: "Work Sans";
	font-weight: 700;
	font-size: 18px;
	letter-spacing: 0.16em;
	text-transform: uppercase;
	color: #159eec;
	margin-bottom: 8px;
	text-align: center;
	@media (max-width: 480px) {
		font-size: 16px;
	}
}
.subtitle-big {
	font-family: "Work Sans";
	font-size: 26px;
	font-weight: 500;
	@media (max-width: 400px) {
		font-size: 22px;
	}
}
.text {
	font-family: "Work Sans";
	font-weight: 400;
	font-size: 16px;
	line-height: 1.4;
	color: #212124;
}
.subtext {
	font-family: "Work Sans";
	font-weight: 400;
	font-size: 18px;
	line-height: 1.4;
}
.img {
	position: relative;
	&::after {
		content: "";
		position: absolute;
		bottom: 0;
		right: 0;
		width: 25%;
		height: 8px;
		background: #159eec;
	}
	&::before {
		content: "";
		position: absolute;
		bottom: 0;
		right: 25%;
		width: 50%;
		height: 8px;
		background: #1f2b6c;
	}
	&__decor {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 25%;
		height: 8px;
		background: #bfd2f8;
	}
}
.swiper-pagination {
	position: relative !important;
	bottom: 0;
	height: 18px;
	margin-top: 32px;
	&-bullet {
		background: #bfd2f8;
		border-radius: 50px;
		width: 18px;
		height: 18px;
		opacity: 1;
		transition: all 0.3s ease;
		&-active {
			background: #1f2b6c;
			transition: all 0.3s ease;
		}
	}
}