.section {
   &__hero {
      background: url(..//img/hero-bg.png) top center no-repeat;
      background-size: cover !important;
      position: relative;
      padding: 167px 0 !important;
      overflow-x: unset !important;
      @media (max-width: 768px) {
         background: url(..//img/hero-bg.png) center right no-repeat;
      }
      &-about,
      &-doctors,
      &-appointment,
      &-contact,
      &-blog,
      &-post,
      &-service {
         .hero__content {
            max-width: 100%;
         }
      }
      &-about,
      &-doctors {
         background: url(..//img/hero-bg-about.png) center center no-repeat;
      }
      &-appointment,
      &-contact {
         background: url(..//img/hero-bg-appointment.png) center center no-repeat;
      }
      &-blog {
         background: url(..//img/hero-bg-blog.png) center center no-repeat;
      }
      &-post {
         background: url(..//img/hero-bg-post.png) center center no-repeat;
         .hero__title {
            margin-bottom: 20px;
         }
      }
      &-service {
         background: url(..//img/hero-bg-service.png) center center no-repeat;
      }
      &-service-item {
         background: url(..//img/hero-bg-service-item.png) center center no-repeat;
      }
   }
}
.hero {
   &__info {
      display: flex;
      align-items: center;
      gap: 24px;
      @media (max-width: 1004px) {
         gap: 18px;
         flex-wrap: wrap;
      }
   }
   &__info-item {
      display: flex;
      align-items: center;
      gap: 8px;
   }
   &__breadcrumps {
      z-index: 2 !important;
      display: flex;
      align-items: center;
      gap: 10px;
   }
   &__link {
      color: #1f2b6c;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         color: #29378b;
      }
   }
   &__content {
      max-width: 519px;
      width: 100%;
   }
   &__title {
      color: #1f2b6c;
      margin-bottom: 32px;
      @media (max-width: 480px) {
         margin-bottom: 20px;
      }
   }
   &__subtitle {
      text-align: left !important;
   }
   &__btn {
      font-weight: 500 !important;
      color: #1f2b6c !important;
      padding: 13px 35px;
      background: #bfd2f8;
      border-radius: 50px;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         background: #a2b3d5;
      }
   }
   &__items {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 21px;
      position: absolute;
      bottom: -50px;
      left: 0;
      right: 0;
      @media (max-width: 768px) {
         flex-wrap: wrap;
         bottom: -50%;
      }
      @media (max-width: 480px) {
         right: 10px;
         left: 10px;
      }
   }
   &__item {
      padding: 25px 20px;
      display: flex;
      align-items: center;
      gap: 56px;
      background: #1f2b6c;
      border-radius: 5px;
      @media (max-width: 400px) {
         gap: 20px;
      }
      &:nth-child(2) {
         background: #bfd2f8;
         .hero__item-text {
            color: #1f2b6c !important;
         }
      }
      &:nth-child(3) {
         background: #159eec;
      }
   }
   &__item-text {
      color: #fcfefe !important;
   }
}
