.contact {
   &__items {
      display: flex;
      justify-content: center;
      gap: 20px;
      @media (max-width: 1004px){
         flex-wrap: wrap;
      }
   } 
   &__item {
      padding: 52px 0 50px 26px;
      background: #bfd2f8;
      border-radius: 5px;
      flex: 0 1 24%;
      height: 233px;
      transition: all .3s ease;
      @media (max-width: 1004px){
        flex: 0 1 46%;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
         text-align: center;
         padding: 50px 0;
       }
      path{
         transition: all .3s ease;
      }
      &:hover{
         transition: all .3s ease;
         background-color: #1f2b6c;
         path{
            transition: all .3s ease;
            stroke: #bfd2f8;
         }
         .contact__item-title, .contact__text{
            color: #bfd2f8 !important;
            transition: all .3s ease;
         }
      } 
   }
   &__item-title {
      color: #1f2b6c !important;
      margin: 16px 0 8px;
      text-align: left !important;
      transition: all .3s ease;
      @media (max-width: 480px){
         text-align: center !important;
      }
   }
   &__text {
      color: #1f2b6c !important;
      transition: all .3s ease;
   }
   a.contact__text{
      color: #1f2b6c !important;
   }
}
