.specialties {
   &__subtitle{
      text-align: center;
   }
   &__items {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 20px;
   }
   &__item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 50px 0;
      text-align: center;
      width: 100%;
      flex: 0 1 23%;
      border-radius: 5px;
      transition: all .3s ease;
      @media (max-width: 1004px){
         flex: 0 1 31%;
      }
      @media (max-width: 768px){
         flex: 0 1 47%;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
      }
      path{
         transition: all .3s ease;
      }
      &:hover{
         transition: all .3s ease;
         background: #1F2B6C;
         .specialties__text{  
            color: #BFD2F8;
         }
         path{
            transition: all .3s ease;
            fill: #BFD2F8;
         }
      }
   }
   &__text {
      transition: all .3s ease;
   }
}
