.section__doctors-doctors{
   .slide{
      flex: 0 1 31%;
      @media (max-width: 768px){
         flex: 0 1 47%;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
      }
   } 
}
.doctors {
   &__items{
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 20px;
   }
   .slide {
      background: #bfd2f8;
      &__img {
         width: 100%;
         height: auto;
         img {
            border-radius: 5px 5px 0 0;
            width: 100%;
            height: auto;
         }
      }
      &__content {
         padding: 24px 0;
         text-align: center;
      }
      &__title {
         font-weight: 700 !important;
         margin-bottom: 16px;
      }
      &__text {
         color: #1f2b6c;
      }
      &__icons {
         display: flex;
         align-items: center;
         justify-content: center;
         gap: 20px;
         a{
            transition: all .3s ease;
            &:hover{
               transition: all .3s ease;
               transform: scale(1.1);
            }
         }
      }
      &__btn {
         background: #1f2b6c;
         border-radius: 0px 0px 5px 5px;
         width: 100%;
         display: flex;
         justify-content: center;
         padding: 12px 0;
         color: #bfd2f8 !important;
         transition: all .3s ease;
         &:hover{
            transition: all .3s ease;
            background: #29378b;
         }
      }
   }
}
