.section__service-about {
   .service {
      &__accordion-item {
         display: flex;
         .title {
            text-align: left !important;
            margin-bottom: 24px !important;
         }
         .subtitle {
            text-align: left !important;
         }
         .service__accordion-content {
            max-width: 500px;
         }
         @media (max-width: 768px) {
            img {
               max-width: 500px;
               width: 100%;
               height: auto;
            }
         }
      }
   }
}
.section__service-services {
   .service {
      &__accordion {
         align-items: flex-start;
         @media (max-width: 768px) {
            flex-direction: column;
         }
      }
      &__accordion-header {
         flex-direction: row;
         justify-content: flex-start;
         padding: 24px 0px 24px 32px;
         @media (max-width: 768px) {
            min-width: 240px;
            width: 100%;
            border-radius: 5px;
         }
      }
      &__accordion-headers {
         max-width: 240px;
         @media (max-width: 768px) {
            max-width: 100%;
            display: flex;
            overflow-x: scroll;
            &::-webkit-scrollbar {
               display: none;
            }
         }
      }
      &__accordion-item {
         flex-direction: column;
         img {
            width: 100%;
            height: auto;
         }
      }
      &__accordion-title {
         text-align: left !important;
         margin-bottom: 24px !important;
      }
      &__accordion-link {
         flex: 0 1 33%;
         @media (max-width: 1004px) {
            flex: 0 1 50%;
         }
         @media (max-width: 480px) {
            flex: 0 1 100%;
         }
      }
   }
}
.service {
   &__accordion {
      display: flex;
      align-items: center;
      gap: 20px;
      @media (max-width: 1004px) {
         gap: 10px;
         align-items: flex-start;
      }
      @media (max-width: 480px) {
         flex-direction: column;
      }
   }
   &__accordion-item {
      display: flex;
      align-items: center;
      gap: 21px;
      display: none;
      @media (max-width: 1004px) {
         gap: 11px;
      }
      @media (max-width: 768px) {
         flex-direction: column;
      }
      &.show{
         display: flex;
      }
   }
   &__accordion-headers {
      border: 1px solid #1f2b6c;
      border-radius: 5px;
      max-width: 157px;
      width: 100%;
      @media (max-width: 480px) {
         max-width: 100%;
         display: flex;
         overflow-x: scroll;
         &::-webkit-scrollbar {
            display: none;
         }
      }
   }
   &__accordion-header {
      background: rgba(252, 254, 254, 0.2);
      border-bottom: none;
      padding: 24px 0;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      gap: 8px;
      text-align: center;
      transition: all 0.3s ease;
      cursor: pointer;
      @media (max-width: 768px) {
         padding: 20px;
      }
      @media (max-width: 480px) {
         min-width: 150px;
         width: 100%;
         border-radius: 5px;
      }
      &:first-child {
         border-radius: 5px 5px 0 0;
      }
      .service__accordion-text {
         transition: all 0.3s ease;
      }
      path {
         transition: all 0.3s ease;
      }
      &.show {
         background: #1f2b6c;
         transition: all 0.3s ease;
         .service__accordion-text {
            color: #bfd2f8;
            transition: all 0.3s ease;
         }
         path {
            fill: #bfd2f8;
            transition: all 0.3s ease;
         }
      }
   }
   &__accordion-content {
      .service__accordion-text {
         margin-bottom: 16px;
      }
   }
   &__accordion-title {
      color: #000000;
      margin-bottom: 24px;
      @media (max-width: 1004px) {
         font-size: 24px;
      }
   }
   &__accordion-list {
      width: 100%;
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      row-gap: 16px;
      margin-left: 26px;
      margin-bottom: 32px;
      @media (max-width: 1004px) {
         margin-left: 20px;
      }
   }
   &__accordion-link {
      flex: 0 1 50%;
      color: #000000;
      display: flex;
      align-items: center;
      position: relative;
      @media (max-width: 1004px) {
         flex: 0 1 100%;
      }
      &::after {
         content: "";
         position: absolute;
         left: -26px;
         width: 16px;
         height: 16px;
         border-radius: 50%;
         background: #159eec;
         @media (max-width: 1004px) {
            left: -20px;
         }
      }
   }
   &__accordion-images {
      min-width: 324px;
      width: 100%;
      display: flex;
      flex-direction: column;
      gap: 21px;
      @media (max-width: 1004px) {
         min-width: 260px;
      }
      @media (max-width: 768px) {
         min-width: max-content;
         min-width: fit-content;
      }
   }
   &__accordion-img {
      max-width: 324px;
      width: 100%;
      height: 100%;
      max-height: 231px;
      @media (max-width: 768px) {
         max-width: 100%;
         max-height: fit-content;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__btn {
      background: #1f2b6c;
      border-radius: 0px 0px 5px 5px;
      font-family: "Work Sans";
      font-weight: 500;
      font-size: 14px;
      color: #bfd2f8;
      padding: 12px 0;
      width: 100%;
      display: flex;
      justify-content: center;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         background: #29378b;
      }
      @media (max-width: 480px) {
         min-width: 150px;
         align-items: center;
      }
   }
}
