nav {
   &.fixed__nav {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 9999;
      .nav {
         transition: all 0.3s ease;
         &__top{
            display: none;
         }
      }
   } 
}
.nav {
   transition: all 0.3s ease;
   &__top {
      padding: 19px 0;
      background: #fff;
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
         @media (max-width: 1004px) {
            justify-content: center;
         }
         .nav__logo {
            @media (max-width: 1004px) {
               display: none;
            }
         }
      }
   }
   &__items {
      display: flex;
      align-items: center;
      gap: 21px;
      @media (max-width: 768px) {
         flex-wrap: wrap;
         justify-content: center;
      }
      @media (max-width: 480px) {
         gap: 16px;
      }
   }
   &__item {
      display: flex;
      align-items: center;
      gap: 5px;
      @media (max-width: 480px) {
         width: 100%;
         max-width: 250px;
         img {
            width: 18px;
         }
      }
   }
   &__item-text {
      font-weight: 500 !important;
      color: #159eec !important;
      @media (max-width: 480px) {
         font-size: 14px;
      }
   }
   &__item-title {
      color: #1f2b6c !important;
      margin-bottom: 2px;
   }
   &__body {
      padding: 17px 0;
      background: #1f2b6c;
      position: relative;
      .container {
         display: flex;
         align-items: center;
         justify-content: space-between;
         .nav__logo {
            display: none;
            @media (max-width: 1004px) {
               display: block;
            }  
         }
      }
   }
   &__list {
      list-style: none;
      display: flex;
      align-items: center;
      gap: 20px;
      @media (max-width: 1004px) {
         flex-direction: column;
         display: none;
      }
   }
   &__list-link {
      color: #fcfefe;
      transition: all 0.3s ease;
      white-space: nowrap;
      @media (max-width: 1004px) {
         font-size: 24px;
         color: #1f2b6c;
         &:hover {
            color: #1f2b6c !important;
         }
      }
      &:hover {
         color: #bfd2f8;
         font-weight: 600;
         transition: all 0.3s ease;
      }
   }
   &__menu {
      @media (max-width: 1004px) {
         transform: translate(0, -100%) scaleY(0);
         transition: all 0.3s ease;
         &.active {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            left: 0;
            top: 100%;
            right: 0;
            width: 100%;
            min-height: 100%;
            padding: 48px 0;
            z-index: 99;
            background: #bfd2f8;
            transition: all 0.3s ease;
            transform: translate(0, 0) scaleY(1);
            .nav__list {
               display: flex;
            }
         }
      }
   }
   &__content {
      display: flex;
      align-items: center;
      gap: 30px;
   }
   &__btn {
      color: #1f2b6c !important;
      padding: 13px 35px;
      background: #bfd2f8;
      border-radius: 50px;
      transition: all .3s ease;
      @media (max-width: 1004px) {
         display: none;
      }
      &:hover{
         transition: all .3s ease;
         background: #a2b3d5;
      }
      &.hide {
         display: none;
         background: #1f2b6c;
         color: #bfd2f8 !important;
         transition: all .3s ease;
         &:hover{
            transition: all .3s ease;
            background: #29378b;
         }
         @media (max-width: 1004px) {
            display: flex;
            min-width: 300px;
            width: 100%;
            justify-content: center;
         }
      }
   }
   &__form {
      display: flex;
      align-items: center;
      gap: 10px;
      @media (max-width: 1004px) {
         margin-right: 30px;
      }
      @media (max-width: 480px) {
         margin-right: 0px;
      }
   }
   &__form-input {
      color: #fcfefe !important;
      padding: 10px;
      background: none;
      transform: translate(50%, 0) scaleX(0);
      transition: all 0.3s ease;
      border: none;
      @media (max-width: 480px) {
         position: absolute;
         left: 0;
         right: 90px;
         background: #1f2b6c;
      }
      &::placeholder {
         color: #fcfefe !important;
      }
      &::-moz-placeholder {
         opacity: 1;
         color: #fcfefe !important;
      }
      &.show {
         transform: translate(0, 0) scaleX(1);
         transition: all 0.3s ease;
      }
   }
   &__form-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      background: inherit;
      border: none;
      cursor: pointer;
   }
   .burger {
      @media (max-width: 1004px) {
         width: 24px;
         height: 17px;
         z-index: 1001;
         position: relative;
         &::after,
         &::before {
            content: "";
            position: absolute;
            width: 100%;
            left: 0;
            height: 2px;
            border-radius: 2px;
            background: #fcfefe;
            transition: all 0.3s ease;
         }
         &.active-burger::before {
            transform: rotate(45deg);
            top: 7px;
         }
         &.active-burger::after {
            transform: rotate(-45deg);
            bottom: 8px;
         }
         &::before {
            top: 0;
         }
         &::after {
            bottom: 0;
         }
         span {
            position: absolute;
            background: #fcfefe;
            width: 100%;
            height: 2px;
            left: 0;
            top: 50%;
            transform: translate(0, -50%);
            transition: all 0.3s ease;
            border-radius: 2px;
         }
         &.active-burger span {
            transform: scale(0);
         }
      }
   }
}
