.section__book-appointment {
   .book {
      gap: 20px;
      @media (max-width: 1004px) {
         flex-direction: column;
         gap: 40px;
         .book__item,
         .book__form {
            max-width: 100%;
         }
      }
      &__title {
         color: #1f2b6c !important;
      }
      &__item {
         margin-bottom: 64px;
      }
      &__shedule {
         background: #1f2b6c;
         border-radius: 5px;
         padding: 48px 50px;
         width: 100%;
         height: 100%;
         flex: 0 1 50%;
         @media (max-width: 480px) {
            padding: 30px 20px;
         }
         @media (max-width: 320px) {
            padding: 20px 10px;
         }
         &-title {
            color: #bfd2f8;
            margin-bottom: 51px;
         }
         a.book__shedule-subtitle {
            color: #bfd2f8 !important;
         }
         &-text {
            color: #fcfefe;
         }
         &-items {
            display: flex;
            flex-direction: column;
            gap: 25px;
            padding-bottom: 32px;
            border-bottom: 1px solid #bfd2f8;
            margin-bottom: 32px;
            @media (max-width: 400px) {
               padding-bottom: 20px;
               margin-bottom: 20px;
            }
         }
         &-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width: 400px) {
               flex-direction: column;
            }
            .break {
               width: 20px;
               height: 2px;
               background: #bfd2f8;
               @media (max-width: 400px) {
                  display: none;
               }
            }
         }
         &-contact {
            display: flex;
            align-items: center;
            gap: 8px;
            justify-content: center;
         }
         &-subtitle {
            color: #fcfefe !important;
            @media (max-width: 768px) {
               text-align: left;
            }

            &:last-child {
               color: #bfd2f8 !important;
            }
         }
      }
   }
}
.section__book-contact {
   .book {
      gap: 20px;
      @media (max-width: 1004px) {
         flex-direction: column;
         gap: 40px;
         .book__items,
         .book__item,
         .book__form {
            max-width: 100% !important;
            width: 100%;
         }
      }
   }
   .book__form-input:nth-child(3) {
      flex: 0 1 100%;
   }
   .book__title {
      color: #1f2b6c !important;
      @media (max-width: 768px) {
         text-align: center;
      }
   }
   .book__subtitle {
      text-align: left !important;
      @media (max-width: 768px) {
         text-align: center !important;
      }
   }
   .contact__items {
      flex-wrap: wrap;
      width: 100%;
   }
   .book__items {
      max-width: 486px;
      width: 100%;
   }
   .contact__item {
      flex: 1 1 25%;
      width: 100%;
      max-width: 233px !important;
      height: auto;
      padding: 50px 30px;
      @media (max-width: 1004px) {
         flex: 1 1 45%;
         max-width: 100% !important;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
   }
}
.book {
   display: flex;
   align-items: center;
   justify-content: center;
   gap: 94px;
   @media (max-width: 1004px) {
      gap: 30px;
   }
   @media (max-width: 768px) {
      flex-direction: column;
      text-align: center;
   }
   &__item {
      max-width: 351px;
      width: 100%;
   }
   &__title {
      margin-bottom: 16px !important;
      color: #159eec !important;
      text-align: left !important;
      @media (max-width: 768px) {
         text-align: center !important;
      }
   }
   &__form {
      display: flex;
      justify-content: center;
      align-items: center;
      max-width: 491px;
      width: 100%;
      flex-wrap: wrap;
      border-radius: 5px;
      background: #1f2b6c;
   }
   &__form-input,
   &__form-select {
      flex: 0 1 50%;
      width: 100%;
      height: 100%;
      @media (max-width: 400px) {
         flex: 0 1 100%;
      }
   }
   &__form-textarea,
   &__form-btn {
      flex: 0 1 100%;
      width: 100%;
      border: 1px solid #fff;
   }
   &__form-input {
      background: none;
      color: #fcfefe !important;
      padding: 16px 20px;
      border: 1px solid #fff;
      &::placeholder {
         color: #fcfefe !important;
      }
      &::-moz-placeholder {
         color: #fcfefe !important;
         opacity: 1;
      }
   }
   input[type="date"],
   input[type="time"] {
      position: relative;
      &::before,
      &::after {
         content: "";
         position: absolute;
         top: 50%;
         right: 16px;
         display: block;
         width: 10px;
         height: 2px;
         transition: all 0.3s ease-out;
         background-color: #ffffff;
         transform: translate(-3px, -50%) rotate(45deg);
      }
      &::after {
         transform: translate(3px, -50%) rotate(-45deg);
      }
      &:focus-within {
         &::before {
            transform: translate(-3px, -50%) rotate(-45deg);
         }
         &::after {
            transform: translate(3px, -50%) rotate(45deg);
         }
      }
      &::-webkit-calendar-picker-indicator {
         cursor: pointer;
         background: none;
      }
   }
   &__form-textarea {
      resize: none;
      height: 190px;
      font-family: "Work Sans", sans-serif;
   }
   &__form-btn {
      padding: 14px 0;
      background: #bfd2f8;
      border-radius: 0px 0px 5px 5px;
      color: #1f2b6c !important;
      font-weight: 500 !important;
      cursor: pointer;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         background: #a2b3d5;
      }
   }
}

.book__form-select {
   position: relative;
   padding: 14px 20px;
   position: relative;
   width: 100%;
   opacity: 1;
   border: 1px solid #ffffff;
   select {
      display: none;
   }
   .select-selected {
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      cursor: pointer;
      color: #fff !important;
      &::before,
      &::after {
         content: "";
         position: absolute;
         top: 50%;
         right: 16px;
         display: block;
         width: 10px;
         height: 2px;
         transition: all 0.3s ease-out;
         background-color: #ffffff;
         transform: translate(-3px, -50%) rotate(45deg);
      }
      &::after {
         transform: translate(3px, -50%) rotate(-45deg);
      }
   }
   .select-arrow {
      &::before {
         transform: translate(-3px, -50%) rotate(-45deg);
      }
      &::after {
         transform: translate(3px, -50%) rotate(45deg);
      }
   }
   div,
   .select-selected {
      cursor: pointer;
      height: 100%;
      user-select: none;
      background-color: #1f2b6c;
   }
   .select-items {
      position: absolute;
      top: 100%;
      left: 0px;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100%;
      background-color: #1f2b6c;
      transition: all 0.3s ease-out;
      z-index: 99 !important;
      div{
         padding: 10px;
         margin-top: -2px;
         color: #ffffff;
         transition: all .3s ease;
         &.same-as-selected, &:hover{
            background-color: #159eec;
            transition: all .3s ease;
         }
      }
   }
   .select-hide {
      display: none;
   }
}