footer {
   background: #1f2b6c;
}
.footer {
   &__body {
      display: flex;
      gap: 33px;
      padding: 70px 0 47px;
      @media (max-width: 1004px) {
         flex-wrap: wrap;
         gap: 20px;
      }
      @media (max-width: 480px) {
         text-align: center;
         gap: 40px;
      }
   }
   &__info {
      max-width: 240px;
      width: 100%;
      @media (max-width: 1004px) {
         max-width: 100%;
         flex: 0 1 47%;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
      img {
         margin-bottom: 18px;
      }
   }
   &__info-text {
      color: #fcfefe;
   }
   &__title {
      font-weight: 700 !important;
      color: #fcfefe;
      margin-bottom: 39px;
      @media (max-width: 1004px) {
         margin-bottom: 20px;
      }
   }
   &__menu {
      @media (max-width: 1004px) {
         flex: 0 1 47%;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
   }
   &__list {
      list-style: none;
      display: flex;
      flex-direction: column;
      gap: 7px;
   }
   &__text {
      color: #fcfefe !important;
   }
   li > a {
      color: #fcfefe !important;
      transition: all 0.3s ease;
      &:hover {
         transition: all 0.3s ease;
         color: #bfd2f8;
      }
   }
   &__contact {
      max-width: 272px;
      width: 100%;
      @media (max-width: 1004px) {
         max-width: 100%;
         flex: 0 1 47%;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
   }
   &__form {
      width: 100%;
      height: 50px;
      display: flex;
      background: #bfd2f8;
      border-radius: 5px;
   }
   &__form-input {
      height: 100%;
      width: 100%;
      color: #1f2b6c !important;
      padding: 0 10px;
      border: none;
      background: none;
      &::placeholder {
         color: #1f2b6c !important;
      }
      &::-moz-placeholder {
         color: #1f2b6c !important;
         opacity: 1;
      }
   }
   &__form-btn {
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      border: none;
      background: none;
      padding: 0 11px;
      cursor: pointer;
   }
   &__copy {
      border-top: 1px solid #bfd2f8;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 46px 0 76px;
      gap: 20px;
      @media (max-width: 768px) {
         padding: 46px 0;
      }
      @media (max-width: 480px) {
         flex-direction: column;
         justify-content: center;
         text-align: center;
      }
   }
   &__icons {
      display: flex;
      align-items: center;
      gap: 20px;
      a{
         transition: all .3s ease;
         &:hover{
            transition: all .3s ease;
            transform: scale(1.1);
         }
      }
   }
}
