.services {
   display: flex;
   justify-content: center;
   align-items: center;
   flex-wrap: wrap;
   gap: 20px;
   &__item {
      flex: 0 1 31%;
      width: 100%;
      @media (max-width: 1004px) {
         flex: 0 1 47%;
      }
      @media (max-width: 480px) {
         flex: 0 1 100%;
      }
   }
   &__item-img {
      position: relative;
      &:hover {
         .services__item-hover {
            width: 100%;
            height: 100%;
            bottom: 0;
            right: 0;
            border-radius: 0;
            background: rgba(31, 43, 108, 0.8);
            transition: all 0.3s ease;
         }
      }
      .img {
         width: 100%;
         height: 100%;
      }
   }
   &__item-content {
      padding: 56px 20px 32px;
      @media (max-width: 768px) {
         padding: 46px 10px 22px;
      }
   }
   &__item-hover {
      background: #1f2b6c;
      border-radius: 50px;
      width: 80px;
      height: 80px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      bottom: -40px;
      right: 20px;
      transition: all 0.3s ease;
   }
   &__item-title {
      color: #1f2b6c !important;
   }
   &__item-text {
      margin: 8px 0 16px;
   }
   &__item-link {
      color: #159eec !important;
      display: inline-flex;
      align-items: center;
      gap: 8px;
      img{
         transition: all 0.3s ease;
      }
      &:hover {
         img{
            transition: all 0.3s ease;
            transform: translate(5px, 0);
         }
      }
   }
}
