.section {
   &__care {
      padding: 114px 0 64px !important;
      @media (max-width: 768px){
         padding: 350px 0 64px !important;
      }
   }
}
.care { 
   &__title {
      margin-bottom: 15px !important;
   }
   &__text {
      text-align: center;
      margin-bottom: 32px;
   }
   &__link {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      color: #159eec !important;
      margin-bottom: 61px;
      img{
         transition: all .3s ease;
      }
      &:hover{
         img{
            transition: all .3s ease;
            transform: translate(5px, 0);
         }
      }
   }
   &__img {
      width: 100%;
      position: relative;
      &::after{
         content: '';
         position: absolute;
         bottom: 0;
         right: 0;
         width: 25%;
         height: 8px;
         background: #159EEC;
      }
      &::before{
         content: '';
         position: absolute;
         bottom: 0;
         right: 25%;
         width: 50%;
         height: 8px;
         background: #1F2B6C;
      }
      &-decor{
         position: absolute;
         bottom: 0;
         left: 0;
         width: 25%;
         height: 8px;
         background: #BFD2F8;
      }
      @media (max-width: 480px){
         height: 150px;
      }
      img{
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
}
