.news {
   .swiper-wrapper {
      flex-direction: row !important;
   }
   .slide {
      margin: 10px !important;
      display: flex;
      align-items: center;
      gap: 20px;
      @media (max-width: 400px) {
         flex-direction: column;
      }
      &__img {
         width: 100%;
         height: auto;
         @media (max-width: 768px) {
            width: auto;
            height: 200px;
         }
         @media (max-width: 400px) {
            width: 100%;
            img{
               border-radius: 5px 5px 5px 5px;
            } 
         }
         img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 5px 0px 0px 5px;
         }
      }
      &__subtitle {
         font-family: "Work Sans";
         font-weight: 400;
         font-size: 14px;
         color: #159eec;
      }
      &__title { 
         color: #212124;
         margin: 8px 0 24px;
      }
      &__info {
         display: flex;
         align-items: center;
         gap: 10px;
      }
      &__item {
         display: flex;
         align-items: center;
         gap: 5px;
      }
      &__item-text {
         color: #212124;
      }
   }
}
